window.name = 'window`s this';


function StudyThis (name) {
  this.name = name;
}
StudyThis.prototype.normal = function () {
  console.log(this.name);
}

StudyThis.prototype.arrow = () => {
  this.name = 'Ïù¥Í≤å Ìï®ÏàòÏòÅÏó≠ÏóêÏÑú ÏÑ†Ïñ∏Ìïú this';
  console.log(this.name);
  console.log(this);
}


